import React, { Component } from 'react'
import { hashHistory } from 'react-router'
import { Row, Col, Input, Checkbox, Form } from 'antd';
const FormItem = Form.Item;

class TransferForm extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        let provinceOps = [];
        if (this.props.provinceList) {
            for (var key in this.props.provinceList) {
                provinceOps.push(<Col span={6} key={key}><Checkbox value={key}>{this.props.provinceList[key]}</Checkbox></Col>)
            }
        }

        return (
            <Row className="settingForm">
                <Form>
                    <Row className="selectItem">
                        <Row className="title">投资类型</Row>
                        <FormItem>
                            {getFieldDecorator('selectedDebtsProjectType', {
                                initialValue: this.props.selectedTransferData.selectedTransferProjectType
                            })(
                                <Checkbox.Group>
                                    <Row>
                                        {this.props.assets.map(item =>
                                            <Col span={6} key={item.key}>
                                                <Checkbox value={parseInt(item.key, 10)}>{item.value}</Checkbox>
                                            </Col>
                                        )}
                                    </Row>
                                </Checkbox.Group>
                                )}
                        </FormItem>
                    </Row>
                    <Row className="selectItem">
                        <Row className="title">投资地区</Row>
                        <FormItem>
                            {getFieldDecorator('selectedDebtsRegion', {
                                initialValue: this.props.selectedTransferData.selectedTransferRegion
                            })(
                                <Checkbox.Group>
                                    <Row>
                                        {provinceOps}
                                    </Row>
                                </Checkbox.Group>
                                )}
                        </FormItem>
                    </Row>
                    <Row className="selectItem">
                        <Col span={4} className="label">单笔投资区间</Col>
                        <Col span={4}>
                            <FormItem>
                                {getFieldDecorator("transferNumMin", {
                                    initialValue: this.props.selectedTransferData.transferNumMin
                                })(
                                    <Input type="text" placeholder="请输入最小值" />
                                )}
                            </FormItem>
                        </Col><Col className="label" span={1} style={{textAlign: 'center'}}>--</Col>
                        <Col span={4}>
                            <FormItem>
                                {getFieldDecorator("transferNumMax", {
                                    initialValue: this.props.selectedTransferData.transferNumMax
                                })(
                                    <Input type="text" placeholder="请输入最大值" />
                                )}
                            </FormItem>
                        </Col><Col className="label" span={1} style={{textAlign: 'center'}}>万元</Col>
                    </Row>
                </Form>
            </Row>
        )
    }
}

const WrappedTransferForm = Form.create()(TransferForm);
module.exports = WrappedTransferForm;


