import React,{Component} from 'react'
import { hashHistory } from 'react-router'
import { Row, Col, Button, Tabs, Input, message } from 'antd';
const TabPane = Tabs.TabPane;
import DebtsForm from './debtsForm.jsx';
import StockForm from './stockForm.jsx';
import TransferForm from './transferForm.jsx';

class SubscribeSetting extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            "debts": [],
            "stock": [],
            "assets": [],
            "industryList": [],
            "province_list": null,
            "selectedDebtsData": {
                "selectedDebtsProjectType": [],
                "selectedDebtsIndustry": [],
                "selectedDebtsRegion": [],
                "debtsNumMin": 0,
                "debtsNumMax": 0,
            },
            "selectedStockData": {
                "selectedStockType": [],
                "selectedStockProjectType": [],
                "selectedStockIndustry": [],
                "selectedStockRegion": [],
                "stockNumMin": 0,
                "stockNumMax": 0,
            },
            "selectedTransferData": {
                "selectedTransferProjectType": [],
                "selectedTransferRegion": [],
                "transferNumMin": 0,
                "transferNumMax": 0,
            },
            "email": ""
        };
        this.currentTabs = "debts",
        ['changeTabs', 'handleSubmit', 'changeEmail'].forEach(m=>this[m] = this[m].bind(this))
    }

    handleSubmit() {
        let that = this;
        let selectedDebtsData = this.debtsFormRef.props.form.getFieldsValue();
        let selectedStockData = this.stockFormRef ? this.stockFormRef.props.form.getFieldsValue() : this.state.selectedStockData;
        let selectedTransferData = this.transferFormRef ? this.transferFormRef.props.form.getFieldsValue() : this.state.selectedTransferData;
        var params = Object.assign({}, selectedDebtsData, selectedStockData, selectedTransferData);
        ajax({
            url: "/project/subscription/updateSetting",
            data: params,
            callback(data) {
                message.success("设置成功!", 1, () => {
                    history.back();
                })
            }
        })
    }

    changeEmail(e) {
        this.setState({ email: e.target.value });
    }

    changeTabs(key) {
        console.log(key);
    }

    componentWillMount() {
        let that = this;
        ajax({
            url: "/project/subscription/getSetting",
            callback(data) {
                that.setState({
                    "debts": data.debts,
                    "stock": data.stock,
                    "assets": data.assets,
                    "industryList": data.industryList,
                    "province_list": data.province_list,
                    "selectedDebtsData": {
                        "selectedDebtsProjectType": data.selectedDebtsProjectType,
                        "selectedDebtsIndustry": data.selectedDebtsIndustry,
                        "selectedDebtsRegion": data.selectedDebtsRegion,
                        "debtsNumMin": data.debtsNumMin,
                        "debtsNumMax": data.debtsNumMax
                    },
                    "selectedStockData": {
                        "selectedStockType": data.selectedStockType,
                        "selectedStockProjectType": data.selectedStockProjectType,
                        "selectedStockIndustry": data.selectedStockIndustry,
                        "selectedStockRegion": data.selectedStockRegion,
                        "stockNumMin": data.stockNumMin,
                        "stockNumMax": data.stockNumMax
                    },
                    "selectedTransferData": {
                        "selectedTransferProjectType":data.selectedTransferProjectType,
                        "selectedTransferRegion": data.selectedTransferRegion,
                        "transferNumMin": data.transferNumMin,
                        "transferNumMax": data.transferNumMax
                    },
                    "email": data.email
                });
            }
        })
    }

    render() {
        return (
            <Row className="subscribeSettingFrame">
                <Row className="menuTitle">订阅资金设置</Row>
                <Tabs defaultActiveKey="debts" onChange={this.changeTabs}>
                    <TabPane tab="债权资金" key="debts">
                        <DebtsForm debts={this.state.debts} selectedDebtsData={this.state.selectedDebtsData} industryList={this.state.industryList} provinceList={this.state.province_list} wrappedComponentRef={(inst) => this.debtsFormRef = inst} />
                    </TabPane>
                    <TabPane tab="股权资金" key="stock">
                        <StockForm stock={this.state.stock} selectedStockData={this.state.selectedStockData} industryList={this.state.industryList} provinceList={this.state.province_list} wrappedComponentRef={(inst) => this.stockFormRef = inst} />
                    </TabPane>
                    <TabPane tab="资产收购资金" key="transfer">
                        <TransferForm assets={this.state.assets} selectedTransferData={this.state.selectedTransferData} provinceList={this.state.province_list} wrappedComponentRef={(inst) => this.transferFormRef = inst} />
                    </TabPane>
                </Tabs>
                <Row className="emailItem">
                    <Col span={4} className="label">我的邮箱:</Col>
                    <Col span={8}>
                        <Input type="text" value={this.state.email} onChange={this.changeEmail} placeholder="请输入个人邮箱" />
                    </Col>
                    <Col span={5} offset={1}>
                        <Button style={{width: '100%'}} type="primary" onClick={this.handleSubmit}>确定订阅</Button>
                    </Col>
                </Row>
            </Row>
        )
    }
}

module.exports = SubscribeSetting;


