import React, { Component } from 'react'
import { hashHistory } from 'react-router'
import { Row, Col, Input, Checkbox, Form } from 'antd';
import ProfitsItem from './profitsItem';
const FormItem = Form.Item;

class StockForm extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        let provinceOps = [];
        if (this.props.provinceList) {
            for (var key in this.props.provinceList) {
                provinceOps.push(<Col span={6} key={key}><Checkbox value={key}>{this.props.provinceList[key]}</Checkbox></Col>)
            }
        }

        return (
            <Row className="settingForm">
                <Form>
                    <Row className="selectItem">
                        <Row className="title">参/控股</Row>
                        <FormItem>
                            {getFieldDecorator('selectedStockType', {
                                initialValue: this.props.selectedStockData.selectedStockType
                            })(
                                <Checkbox.Group>
                                    <Row>
                                        <Col span={6} key="1">
                                            <Checkbox value={1}>参股型项目</Checkbox>
                                        </Col>
                                        <Col span={6} key="2">
                                            <Checkbox value={2}>控股型项目</Checkbox>
                                        </Col>
                                    </Row>
                                </Checkbox.Group>
                                )}
                        </FormItem>
                    </Row>
                    <Row className="selectItem">
                        <Row className="title">投资类型</Row>
                        <FormItem>
                            {getFieldDecorator('selectedStockProjectType', {
                                initialValue: this.props.selectedStockData.selectedStockProjectType
                            })(
                                <Checkbox.Group>
                                    <Row>
                                        {this.props.stock.map(item =>
                                            <Col span={6} key={item.key}>
                                                <Checkbox value={parseInt(item.key, 10)}>{item.value}</Checkbox>
                                            </Col>
                                        )}
                                    </Row>
                                </Checkbox.Group>
                                )}
                        </FormItem>
                    </Row>
                    <Row className="selectItem">
                        <Row className="title">投资行业</Row>
                        <FormItem>
                            {getFieldDecorator('selectedStockIndustry', {
                                initialValue: this.props.selectedStockData.selectedStockIndustry
                            })(
                                <Checkbox.Group>
                                    <Row>
                                        {this.props.industryList.map(item =>
                                            <Col span={6} key={item.key}>
                                                <Checkbox value={parseInt(item.key, 10)}>{item.value}</Checkbox>
                                            </Col>
                                        )}
                                    </Row>
                                </Checkbox.Group>
                                )}
                        </FormItem>
                    </Row>
                    <Row className="selectItem">
                        <Row className="title">投资地区</Row>
                        <FormItem>
                            {getFieldDecorator('selectedStockRegion', {
                                initialValue: this.props.selectedStockData.selectedStockRegion
                            })(
                                <Checkbox.Group>
                                    <Row>
                                        {provinceOps}
                                    </Row>
                                </Checkbox.Group>
                                )}
                        </FormItem>
                    </Row>
                    <Row className="selectItem">
                        <Col span={4} className="label">单笔投资区间</Col>
                        <Col span={4}>
                            <FormItem>
                                {getFieldDecorator("stockNumMin", {
                                    initialValue: this.props.selectedStockData.stockNumMin
                                })(
                                    <Input type="text" placeholder="请输入最小值" />
                                    )}
                            </FormItem>
                        </Col><Col className="label" span={1} style={{ textAlign: 'center' }}>--</Col>
                        <Col span={4}>
                            <FormItem>
                                {getFieldDecorator("stockNumMax", {
                                    initialValue: this.props.selectedStockData.stockNumMax
                                })(
                                    <Input type="text" placeholder="请输入最大值" />
                                    )}
                            </FormItem>
                        </Col><Col className="label" span={1} style={{ textAlign: 'center' }}>万元</Col>
                    </Row>
                </Form>
            </Row>
        )
    }
}

const WrappedStockForm = Form.create()(StockForm);
module.exports = WrappedStockForm


